.container {
  width: 1280px;
  /* display: flex; */
  /* gap: 24px; */
  /* flex-direction: row; */
  align-items: center;
  /* margin-left: 100px; */
  padding-left: 130px;
}

@media screen and (max-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (max-width: 375px) {
  .container {
    width: 375px;
  }
}

.cont {
  width: 1280px;
  background-image: url('/public/cont.png');
  opacity: 0.5;
  height: 700px;
}

@media screen and (max-width: 768px) {
  .cont {
    width: 768px;
  }
}

@media screen and (max-width: 375px) {
  .cont {
    width: 375px;
  }
}

.title {
  width: 1280px;
  font-weight: 600;
  font-size: 50px;
  margin-bottom: 24px;
  line-height: 1.2;
  text-align: center;
  margin-right: 50px;
  padding-bottom: 30px;
}

@media screen and (max-width: 768px) {
  .title {
    width: 768px;
    font-size: 30px;
  }
}

@media screen and (max-width: 375px) {
  .title {
    width: 375px;
    font-size: 25px;
  }
}

